import { FocusMonitor } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { ENTER, LEFT_ARROW, RIGHT_ARROW, SPACE } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { BooleanInput, NzSizeDSType, OnChangeType, OnTouchedType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'switch';

@Component({
  selector: 'bps-switch',
  exportAs: 'bpsSwitch',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsSwitchComponent),
      multi: true
    }
  ],
  styleUrls: ['./bps-switch.component.css'],
  styles: [
    `
      bps-switch {
        display: inline-block;
      }
    `
  ],
  template: `
<ng-container *ngIf="!bpsDual">
  <button
      nz-wave
      type="button"
      class="ant-switch"
      #switchElement
      [attr.id]="bpsId"
      [disabled]="bpsDisabled"
      [class.ant-switch-checked]="isChecked"
      [class.ant-switch-loading]="bpsLoading"
      [class.ant-switch-disabled]="bpsDisabled"
      [class.ant-switch-small]="bpsSize === 'small'"
      [class.ant-switch-rtl]="dir === 'rtl'"
      [nzWaveExtraNode]="true"
    >
      <span class="ant-switch-handle">
        <span *ngIf="bpsLoading" nz-icon nzType="loading" class="ant-switch-loading-icon"></span>
      </span>
      <span class="ant-switch-inner">
        <ng-container *ngIf="isChecked; else uncheckTemplate">
          <ng-container *nzStringTemplateOutlet="bpsCheckedChildren">{{ bpsCheckedChildren }}</ng-container>
        </ng-container>
        <ng-template #uncheckTemplate>
          <ng-container *nzStringTemplateOutlet="bpsUnCheckedChildren">{{ bpsUnCheckedChildren }}</ng-container>
        </ng-template>
      </span>
      <div class="ant-click-animating-node"></div>
    </button>
</ng-container>
<ng-container *ngIf="bpsDual">
  <button type="button" #switchElement
          class="ant-switch bps-switch"
          [disabled]="bpsDisabled"
          [class.bps-switch-disabled]="bpsDisabled"
          (keydown)="onKeyDown($event)">
    <ng-container *ngIf="bpsDualValues.length > 1">
      <div class="bps-switch-inner">
        <div class="bps-switch-node bps-switch-node-left"
             (click)="checkNode(bpsDualValues[0], bpsDualValues[1])"
             [class.bps-switch-checked]="bpsDualValues[0].checked">
          {{ bpsDualValues[0].title }}
        </div>
        <div class="bps-switch-node bps-switch-node-right"
             (click)="checkNode(bpsDualValues[1], bpsDualValues[0])"
             [class.bps-switch-checked]="bpsDualValues[1].checked">
          {{ bpsDualValues[1].title }}
        </div>
      </div>  
    </ng-container>
  </button>
</ng-container>
  `
})
export class BpsSwitchComponent implements ControlValueAccessor, AfterViewInit, OnDestroy, OnInit {
  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;

  static ngAcceptInputType_bpsLoading: BooleanInput;
  static ngAcceptInputType_bpsDisabled: BooleanInput;
  static ngAcceptInputType_bpsControl: BooleanInput;

  isChecked = false;
  onChange: OnChangeType = () => { };
  onTouched: OnTouchedType = () => { };
  @ViewChild('switchElement', { static: true }) switchElement!: ElementRef<HTMLElement>;
  @Input() @InputBoolean() bpsLoading = false;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsControl = false;
  @Input() bpsCheckedChildren: string | TemplateRef<void> | null = null;
  @Input() bpsUnCheckedChildren: string | TemplateRef<void> | null = null;
  @Input() @WithConfig() bpsSize: NzSizeDSType = 'default';
  @Input() bpsId: string | null = null;

  @Input() @InputBoolean() bpsDual = false;
  @Input() bpsDualValues: { title: string; checked: boolean }[] = [];
  @Output() bpsDualValuesChange: EventEmitter<{ title: string; checked: boolean }[]> = new EventEmitter();

  dir: Direction = 'ltr';

  private destroy$ = new Subject<void>();
  private isNzDisableFirstChange = true;

  updateValue(value: boolean): void {
    if (this.isChecked !== value) {
      this.isChecked = value;
      this.onChange(this.isChecked);
    }
  }

  focus(): void {
    this.focusMonitor.focusVia(this.switchElement?.nativeElement, 'keyboard');
  }

  blur(): void {
    this.switchElement?.nativeElement.blur();
  }

  constructor(
    public nzConfigService: NzConfigService,
    private host: ElementRef<HTMLElement>,
    private ngZone: NgZone,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor,
    @Optional() private directionality: Directionality
  ) { }

  ngOnInit(): void {
    this.directionality.change.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;

    this.ngZone.runOutsideAngular(() => {
      fromEvent(this.host.nativeElement, 'click')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => {
          event.preventDefault();

          if (this.bpsControl || this.bpsDisabled || this.bpsLoading) {
            return;
          }

          this.ngZone.run(() => {
            this.updateValue(!this.isChecked);
            this.cdr.markForCheck();
          });
        });

      if (this.switchElement === null || this.switchElement === undefined) return;
      fromEvent<KeyboardEvent>(this.switchElement?.nativeElement, 'keydown')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => {
          if (this.bpsControl || this.bpsDisabled || this.bpsLoading) {
            return;
          }

          const { keyCode } = event;
          if (keyCode !== LEFT_ARROW && keyCode !== RIGHT_ARROW && keyCode !== SPACE && keyCode !== ENTER) {
            return;
          }

          event.preventDefault();

          this.ngZone.run(() => {
            if (keyCode === LEFT_ARROW) {
              this.updateValue(false);
            } else if (keyCode === RIGHT_ARROW) {
              this.updateValue(true);
            } else if (keyCode === SPACE || keyCode === ENTER) {
              this.updateValue(!this.isChecked);
            }

            this.cdr.markForCheck();
          });
        });
    });
  }

  ngAfterViewInit(): void {
    if (this.switchElement === null || this.switchElement === undefined) return;
    this.focusMonitor
      .monitor(this.switchElement?.nativeElement, true)
      .pipe(takeUntil(this.destroy$))
      .subscribe(focusOrigin => {
        if (!focusOrigin) {
          /** https://github.com/angular/angular/issues/17793 **/
          Promise.resolve().then(() => this.onTouched());
        }
      });
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.switchElement?.nativeElement);
    this.destroy$.next();
    this.destroy$.complete();
  }

  checkNode(nodeA: { title: string; checked: boolean }, nodeB: { title: string; checked: boolean }) {
    if (!this.bpsDisabled) {
      nodeA.checked = true;
      nodeB.checked = false;
      this.bpsDualValuesChange.emit(this.bpsDualValues);
    }
  }

  onKeyDown(e: KeyboardEvent): void {
    if (!this.bpsControl && !this.bpsDisabled && !this.bpsLoading) {
      if (e.keyCode === LEFT_ARROW) {
        this.updateValue(false);
        e.preventDefault();
      } else if (e.keyCode === RIGHT_ARROW) {
        this.updateValue(true);
        e.preventDefault();
      } else if (e.keyCode === SPACE || e.keyCode === ENTER) {
        this.updateValue(!this.isChecked);
        e.preventDefault();
      }
    }
  }

  writeValue(value: boolean): void {
    this.isChecked = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }

  setDisabledState(disabled: boolean): void {
    this.bpsDisabled = (this.isNzDisableFirstChange && this.bpsDisabled) || disabled;
    this.isNzDisableFirstChange = false;
    this.cdr.markForCheck();
  }
}
