import { Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { BooleanInput, NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { BpsToolTipComponent } from '../bps-tooltip/tooltip';
import {
  isTooltipEmpty,
  NzTooltipBaseDirective,
  NzTooltipTrigger,
  PropertyMapping
} from './../bps-tooltip/base';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'popover';

export type BpsPopoverType = 'variation_1' | 'variation_2' | 'variation_3' | 'variation_4' | 'variation_5' | 'variation_6' | 'variation_7a' |
  'variation_7b' | 'variation_8a' | 'variation_8b' | 'variation_9a' | 'variation_9b' | 'variation_10' | 'variation_11';

@Directive({
  selector: '[bps-popover]',
  exportAs: 'bpsPopover',
  host: {
    '[class.ant-popover-open]': 'visible'
  }
})
export class BpsPopoverDirective extends NzTooltipBaseDirective {
  static ngAcceptInputType_bpsPopoverArrowPointAtCenter: BooleanInput;

  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;

  @Input('bpsPopoverArrowPointAtCenter') @InputBoolean() override arrowPointAtCenter?: boolean;
  @Input('bpsPopoverTitle') override title?: NzTSType;
  @Input('bpsPopoverContent') override content?: NzTSType;
  @Input('bps-popover') override directiveTitle?: NzTSType | null;
  @Input('bpsPopoverTrigger') override trigger?: NzTooltipTrigger = 'hover';
  @Input('bpsPopoverPlacement') override placement?: string | string[] = 'top';
  @Input('bpsPopoverOrigin') override origin?: ElementRef<HTMLElement>;
  @Input('bpsPopoverVisible') override visible?: boolean;
  @Input('bpsPopoverMouseEnterDelay') override mouseEnterDelay?: number;
  @Input('bpsPopoverMouseLeaveDelay') override mouseLeaveDelay?: number;
  @Input('bpsPopoverOverlayClassName') override overlayClassName?: string;
  @Input('bpsPopoverOverlayStyle') override overlayStyle?: NgStyleInterface;
  @Input('bpsPopoverType') popoverType: BpsPopoverType = 'variation_1';

  @Input() @WithConfig() bpsPopoverBackdrop?: boolean = false;

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('bpsPopoverVisibleChange') override readonly visibleChange = new EventEmitter<boolean>();

  override componentRef: ComponentRef<BpsPopoverComponent> = this.hostView.createComponent(BpsPopoverComponent);

  protected override getProxyPropertyMap(): PropertyMapping {
    return {
      bpsPopoverBackdrop: ['bpsBackdrop', () => this.bpsPopoverBackdrop],
      ...super.getProxyPropertyMap()
    };
  }

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective,
    nzConfigService?: NzConfigService
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation, nzConfigService);
  }
}

@Component({
  selector: 'bps-popover',
  exportAs: 'nzPopoverComponent',
  animations: [zoomBigMotion],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  styleUrls: ['bps-popover.component.css'],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayHasBackdrop]="hasBackdrop"
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPush]="true"
      [nzArrowPointAtCenter]="bpsArrowPointAtCenter"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-popover"
        [class.ant-popover-rtl]="dir === 'rtl'"
        [ngClass]="_classMap"
        [ngStyle]="bpsOverlayStyle"
        [@.disabled]="!!noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-popover-content">
          <div class="ant-popover-arrow bps-popover-arrow-{{bpsPopoverType}}">
            <span class="ant-popover-arrow-content bps-popover-inner-{{bpsPopoverType}}"></span>
          </div>
          <div class="ant-popover-inner" role="tooltip">
            <div>
              <div class="ant-popover-title" *ngIf="bpsTitle">
                <ng-container *nzStringTemplateOutlet="bpsTitle">{{ bpsTitle }}</ng-container>
              </div> 
              <div class="ant-popover-inner-content bps-popover-inner-content-{{bpsPopoverType}}">
                <ng-container *nzStringTemplateOutlet="bpsContent">{{ bpsContent }}</ng-container>
              </div>
            </div>
          </div>
        </div>
      </div>
    </ng-template>
  `
})
export class BpsPopoverComponent extends BpsToolTipComponent {
  override _prefix = 'ant-popover';

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }

  get hasBackdrop(): boolean {
    return this.bpsTrigger === 'click' ? this.bpsBackdrop : false;
  }

  protected override isEmpty(): boolean {
    return isTooltipEmpty(this.bpsTitle) && isTooltipEmpty(this.bpsContent);
  }
}
