import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ElementRef,
  Input,
  isDevMode,
  OnDestroy,
  Renderer2
} from '@angular/core';
import { EMPTY, merge, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';

import { isNotNil } from 'ng-zorro-antd/core/util';

import { BpsInputDirective } from './bps-input.directive';

@Component({
  selector: 'bps-textarea-count',
  template: ` <ng-content select="textarea[bps-input]"></ng-content> `,
  host: {
    class: 'ant-input-textarea-show-count'
  },
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BpsTextareaCountComponent implements AfterContentInit, OnDestroy {
  @ContentChild(BpsInputDirective, { static: true }) nzInputDirective!: BpsInputDirective;
  @Input() bpsMaxCharacterCount: number = 0;
  @Input() bpsComputeCharacterCount: (v: string) => number = v => v.length;
  @Input() bpsFormatter: (cur: number, max: number) => string = (c, m) => `${c}${m > 0 ? `/${m}` : ``}`;

  private configChange$ = new Subject();
  private destroy$ = new Subject<boolean>();

  constructor(private renderer: Renderer2, private elementRef: ElementRef<HTMLElement>) { }

  ngAfterContentInit(): void {
    if (!this.nzInputDirective && isDevMode()) {
      throw new Error('[nz-textarea-count]: Could not find matching textarea[nz-input] child.');
    }

    if (this.nzInputDirective.ngControl) {
      const valueChanges = this.nzInputDirective.ngControl.valueChanges || EMPTY;
      merge(valueChanges, this.configChange$)
        .pipe(
          takeUntil(this.destroy$),
          map(() => this.nzInputDirective.ngControl.value),
          startWith(this.nzInputDirective.ngControl.value as string)
        )
        .subscribe(value => {
          this.setDataCount(value);
        });
    }
  }

  setDataCount(value: string): void {
    const inputValue = isNotNil(value) ? String(value) : '';
    const currentCount = this.bpsComputeCharacterCount(inputValue);
    const dataCount = this.bpsFormatter(currentCount, this.bpsMaxCharacterCount);
    this.renderer.setAttribute(this.elementRef.nativeElement, 'data-count', dataCount);
  }

  ngOnDestroy(): void {
    this.configChange$.complete();
    this.destroy$.next(true);
    this.destroy$.complete();
  }
}
