import { ESCAPE, hasModifierKey } from '@angular/cdk/keycodes';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  AfterViewInit,
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  Renderer2,
  SimpleChanges,
  ViewContainerRef
} from '@angular/core';
import { BehaviorSubject, EMPTY, Subject, combineLatest, fromEvent, merge } from 'rxjs';
import { auditTime, distinctUntilChanged, filter, map, mapTo, switchMap, takeUntil } from 'rxjs/operators';

import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { POSITION_MAP } from 'ng-zorro-antd/core/overlay';
import { BooleanInput, IndexableObject } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

import { BpsDropdownMenuComponent, NzPlacementType } from './dropdown-menu.component';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'dropDown';

const listOfPositions = [
  POSITION_MAP.bottomLeft,
  POSITION_MAP.bottomRight,
  POSITION_MAP.topRight,
  POSITION_MAP.topLeft
];

@Directive({
  selector: '[bps-dropdown]',
  exportAs: 'bpsDropdown',
  host: {
    class: 'ant-dropdown-trigger'
  }
})
export class BpsDropDownDirective implements AfterViewInit, OnDestroy, OnChanges {
  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;

  static ngAcceptInputType_bpsBackdrop: BooleanInput;
  static ngAcceptInputType_bpsClickHide: BooleanInput;
  static ngAcceptInputType_bpsDisabled: BooleanInput;
  static ngAcceptInputType_bpsVisible: BooleanInput;

  private portal?: TemplatePortal;
  private overlayRef: OverlayRef | null = null;
  private destroy$ = new Subject<boolean>();
  private positionStrategy = this.overlay
    .position()
    .flexibleConnectedTo(this.elementRef.nativeElement)
    .withLockedPosition()
    .withTransformOriginOn('.ant-dropdown');
  private inputVisible$ = new BehaviorSubject<boolean>(false);
  private bpsTrigger$ = new BehaviorSubject<'click' | 'hover'>('hover');
  private overlayClose$ = new Subject<boolean>();
  @Input() bpsDropdownMenu: BpsDropdownMenuComponent | null = null;
  @Input() bpsTrigger: 'click' | 'hover' = 'hover';
  @Input() bpsMatchWidthElement: ElementRef | null = null;
  @Input() @WithConfig<boolean>() @InputBoolean() bpsBackdrop = false;
  @Input() @InputBoolean() bpsClickHide = true;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsVisible = false;
  @Input() bpsOverlayClassName: string = '';
  @Input() bpsOverlayStyle: IndexableObject = {};
  @Input() bpsPlacement: NzPlacementType = 'bottomLeft';
  @Output() readonly bpsVisibleChange: EventEmitter<boolean> = new EventEmitter();

  setDropdownMenuValue<T extends keyof BpsDropdownMenuComponent>(key: T, value: BpsDropdownMenuComponent[T]): void {
    if (this.bpsDropdownMenu) {
      this.bpsDropdownMenu.setValue(key, value);
    }
  }

  constructor(
    public readonly nzConfigService: NzConfigService,
    public elementRef: ElementRef,
    private overlay: Overlay,
    private renderer: Renderer2,
    private viewContainerRef: ViewContainerRef,
    private platform: Platform
  ) { }

  ngAfterViewInit(): void {
    if (this.bpsDropdownMenu) {
      const nativeElement: HTMLElement = this.elementRef.nativeElement;

      this.bpsDropdownMenu.bpsPlacement = this.bpsPlacement;

      /** host mouse state **/
      const hostMouseState$ = merge(
        fromEvent(nativeElement, 'mouseenter').pipe(mapTo(true)),
        fromEvent(nativeElement, 'mouseleave').pipe(mapTo(false))
      );
      /** menu mouse state **/
      const menuMouseState$ = this.bpsDropdownMenu.mouseState$;
      /** merged mouse state **/
      const mergedMouseState$ = merge(menuMouseState$, hostMouseState$);
      /** host click state **/
      const hostClickState$ = fromEvent(nativeElement, 'click').pipe(map(() => !this.bpsVisible));
      /** visible state switch by bpsTrigger **/
      const visibleStateByTrigger$ = this.bpsTrigger$.pipe(
        switchMap(trigger => {
          if (trigger === 'hover') {
            return mergedMouseState$;
          } else if (trigger === 'click') {
            return hostClickState$;
          } else {
            return EMPTY;
          }
        })
      );
      const descendantMenuItemClick$ = this.bpsDropdownMenu.descendantMenuItemClick$.pipe(
        filter(() => this.bpsClickHide),
        mapTo(false)
      );
      const domTriggerVisible$ = merge(visibleStateByTrigger$, descendantMenuItemClick$, this.overlayClose$).pipe(
        filter(() => !this.bpsDisabled)
      );
      const visible$ = merge(this.inputVisible$, domTriggerVisible$);
      combineLatest([visible$, this.bpsDropdownMenu.isChildSubMenuOpen$])
        .pipe(
          map(([visible, sub]) => visible || sub),
          auditTime(150),
          distinctUntilChanged(),
          filter(() => this.platform.isBrowser),
          takeUntil(this.destroy$)
        )
        .subscribe((visible: boolean) => {
          const element = this.bpsMatchWidthElement ? this.bpsMatchWidthElement.nativeElement : nativeElement;
          const triggerWidth = element.getBoundingClientRect().width;
          if (this.bpsVisible !== visible) {
            this.bpsVisibleChange.emit(visible);
          }
          this.bpsVisible = visible;
          if (visible) {
            /** set up overlayRef **/
            if (!this.overlayRef) {
              /** new overlay **/
              this.overlayRef = this.overlay.create({
                positionStrategy: this.positionStrategy,
                minWidth: triggerWidth,
                disposeOnNavigation: true,
                hasBackdrop: this.bpsBackdrop && this.bpsTrigger === 'click',
                scrollStrategy: this.overlay.scrollStrategies.reposition()
              });
              merge(
                this.overlayRef.backdropClick(),
                this.overlayRef.detachments(),
                this.overlayRef
                  .outsidePointerEvents()
                  .pipe(filter((e: MouseEvent) => !this.elementRef.nativeElement.contains(e.target))),
                this.overlayRef.keydownEvents().pipe(filter(e => e.keyCode === ESCAPE && !hasModifierKey(e)))
              )
                .pipe(takeUntil(this.destroy$))
                .subscribe(() => {
                  this.overlayClose$.next(false);
                });
            } else {
              /** update overlay config **/
              const overlayConfig = this.overlayRef.getConfig();
              overlayConfig.minWidth = triggerWidth;
            }
            /** open dropdown with animation **/
            this.positionStrategy.withPositions([POSITION_MAP[this.bpsPlacement], ...listOfPositions]);
            /** reset portal if needed **/
            if (!this.portal || this.portal.templateRef !== this.bpsDropdownMenu!.templateRef) {
              this.portal = new TemplatePortal(this.bpsDropdownMenu!.templateRef, this.viewContainerRef);
            }
            this.overlayRef.attach(this.portal);
          } else {
            /** detach overlayRef if needed **/
            if (this.overlayRef) {
              this.overlayRef.detach();
            }
          }
        });

      this.bpsDropdownMenu!.animationStateChange$.pipe(takeUntil(this.destroy$)).subscribe(event => {
        if (event.toState === 'void') {
          if (this.overlayRef) {
            this.overlayRef.dispose();
          }
          this.overlayRef = null;
        }
      });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
    if (this.overlayRef) {
      this.overlayRef.dispose();
      this.overlayRef = null;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { bpsVisible, bpsDisabled, bpsOverlayClassName, bpsOverlayStyle, bpsTrigger } = changes;
    if (bpsTrigger) {
      this.bpsTrigger$.next(this.bpsTrigger);
    }
    if (bpsVisible) {
      this.inputVisible$.next(this.bpsVisible);
    }
    if (bpsDisabled) {
      const nativeElement = this.elementRef.nativeElement;
      if (this.bpsDisabled) {
        this.renderer.setAttribute(nativeElement, 'disabled', '');
        this.inputVisible$.next(false);
      } else {
        this.renderer.removeAttribute(nativeElement, 'disabled');
      }
    }
    if (bpsOverlayClassName) {
      this.setDropdownMenuValue('bpsOverlayClassName', this.bpsOverlayClassName);
    }
    if (bpsOverlayStyle) {
      this.setDropdownMenuValue('bpsOverlayStyle', this.bpsOverlayStyle);
    }
  }
}
