import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  SimpleChanges,
  ViewEncapsulation,
  forwardRef
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { BooleanInput, NzSafeAny, NzSizeLDSType, OnChangeType, OnTouchedType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { NzRadioService } from 'ng-zorro-antd/radio';

export type NzRadioButtonStyle = 'outline' | 'solid';

@Component({
  selector: 'bps-radio-group',
  exportAs: 'bpsRadioGroup',
  preserveWhitespaces: false,
  template: ` <ng-content></ng-content> `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    NzRadioService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsRadioGroupComponent),
      multi: true
    }
  ],
  styleUrls: ['./bps-radio-button.component.css'],
  host: {
    class: 'ant-radio-group',
    '[class.ant-radio-group-large]': `bpsSize === 'large'`,
    '[class.ant-radio-group-small]': `bpsSize === 'small'`,
    '[class.ant-radio-group-solid]': `bpsButtonStyle === 'solid'`,
    '[class.ant-radio-group-rtl]': `dir === 'rtl'`,
  }
})
export class BpsRadioGroupComponent implements OnInit, ControlValueAccessor, OnDestroy, OnChanges {
  static ngAcceptInputType_bpsDisabled: BooleanInput;

  private value: NzSafeAny | null = null;
  private destroy$ = new Subject<boolean>();
  private isNzDisableFirstChange: boolean = true;
  onChange: OnChangeType = () => {};
  onTouched: OnTouchedType = () => {};
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() bpsButtonStyle: NzRadioButtonStyle = 'outline';
  @Input() bpsSize: NzSizeLDSType = 'default';
  @Input() bpsName: string | null = null;

  dir: Direction = 'ltr';

  constructor(
    private cdr: ChangeDetectorRef,
    private nzRadioService: NzRadioService,
    @Optional() private directionality: Directionality
  ) {}

  ngOnInit(): void {
    this.nzRadioService.selected$.pipe(takeUntil(this.destroy$)).subscribe(value => {
      if (this.value !== value) {
        this.value = value;
        this.onChange(this.value);
      }
    });
    this.nzRadioService.touched$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      Promise.resolve().then(() => this.onTouched());
    });

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { bpsDisabled, bpsName } = changes;
    if (bpsDisabled) {
      this.nzRadioService.setDisabled(this.bpsDisabled);
    }
    if (bpsName) {
      this.nzRadioService.setName(this.bpsName!);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  writeValue(value: NzSafeAny): void {
    this.value = value;
    this.nzRadioService.select(value);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.bpsDisabled = (this.isNzDisableFirstChange && this.bpsDisabled) || isDisabled;
    this.isNzDisableFirstChange = false;
    this.nzRadioService.setDisabled(this.bpsDisabled);
    this.cdr.markForCheck();
  }
}
