import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  OnInit,
  Optional,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';

import { NzDestroyService } from 'ng-zorro-antd/core/services';
import { BooleanInput, NzSafeAny } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

import { BpsOptionGroupComponent } from './option-group.component';

@Component({
  selector: 'bps-option',
  exportAs: 'bpsOption',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [NzDestroyService],
  template: `
    <ng-template>
      <ng-content></ng-content>
    </ng-template>
  `
})
export class BpsOptionComponent implements OnChanges, OnInit {
  static ngAcceptInputType_bpsDisabled: BooleanInput;
  static ngAcceptInputType_bpsHide: BooleanInput;
  static ngAcceptInputType_bpsCustomContent: BooleanInput;

  changes = new Subject<void>();
  groupLabel: string | number | TemplateRef<NzSafeAny> | null = null;
  @ViewChild(TemplateRef, { static: true }) template!: TemplateRef<NzSafeAny>;
  @Input() bpsTitle?: string | number | null;
  @Input() bpsLabel: string | number | null = null;
  @Input() bpsValue: NzSafeAny | null = null;
  @Input() bpsKey?: string | number;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsHide = false;
  @Input() @InputBoolean() bpsCustomContent = false;

  constructor(@Optional() private nzOptionGroupComponent: BpsOptionGroupComponent, private destroy$: NzDestroyService) {}

  ngOnInit(): void {
    if (this.nzOptionGroupComponent) {
      this.nzOptionGroupComponent.changes.pipe(startWith(true), takeUntil(this.destroy$)).subscribe(() => {
        this.groupLabel = this.nzOptionGroupComponent.bpsLabel;
      });
    }
  }

  ngOnChanges(): void {
    this.changes.next();
  }
}
