import { Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';

import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { isPresetColor, NzPresetColor } from 'ng-zorro-antd/core/color';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { BooleanInput, NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { BpsPopoverType } from '../bps-popover/bps-popover';

import {
  isTooltipEmpty,
  NzTooltipBaseComponent,
  NzTooltipBaseDirective,
  NzTooltipTrigger,
  PropertyMapping
} from './base';

@Directive({
  selector: '[bps-tooltip]',
  exportAs: 'bpsTooltip',
  host: {
    '[class.ant-tooltip-open]': 'visible',
    '[class.bps-tooltip-disabled]': 'tooltipDisabled'
  }
})
export class BpsTooltipDirective extends NzTooltipBaseDirective {
  static ngAcceptInputType_bpsTooltipArrowPointAtCenter: BooleanInput;

  @Input('bpsTooltipTitle') override title?: NzTSType | null;
  @Input('bpsTooltipTitleContext') titleContext?: Object | null = null;
  @Input('bps-tooltip') override directiveTitle?: NzTSType | null;
  @Input('bpsTooltipTrigger') override trigger?: NzTooltipTrigger = 'hover';
  @Input('bpsTooltipPlacement') override placement?: string | string[] = 'top';
  @Input('bpsTooltipOrigin') override origin?: ElementRef<HTMLElement>;
  @Input('bpsTooltipVisible') override visible?: boolean;
  @Input('bpsTooltipMouseEnterDelay') override mouseEnterDelay?: number;
  @Input('bpsTooltipMouseLeaveDelay') override mouseLeaveDelay?: number;
  @Input('bpsTooltipOverlayClassName') override overlayClassName?: string;
  @Input('bpsTooltipOverlayStyle') override overlayStyle?: NgStyleInterface;
  @Input('bpsTooltipArrowPointAtCenter') @InputBoolean() override arrowPointAtCenter?: boolean;
  @Input() bpsTooltipColor?: string;
  @Input('bpsPopoverType') popoverType: BpsPopoverType = 'variation_8a';
  @Input('bpsTooltipType') tooltipType: BpsPopoverType = 'variation_8a';
  @Input('bpsTooltipDisabled') @InputBoolean() tooltipDisabled = false;

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('bpsTooltipVisibleChange') override readonly visibleChange = new EventEmitter<boolean>();

  override componentRef: ComponentRef<BpsToolTipComponent> = this.hostView.createComponent(BpsToolTipComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation);
  }

  protected override getProxyPropertyMap(): PropertyMapping {
    return {
      ...super.getProxyPropertyMap(),
      bpsTooltipColor: ['bpsColor', () => this.bpsTooltipColor],
      bpsTooltipTitleContext: ['bpsTitleContext', () => this.titleContext]
    };
  }
}

@Component({
  selector: 'bps-tooltip',
  exportAs: 'bpsTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./bps-tooltip.component.css'],
  animations: [zoomBigMotion],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayPush]="true"
      [nzArrowPointAtCenter]="bpsArrowPointAtCenter"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-tooltip"
        [class.ant-tooltip-rtl]="dir === 'rtl'"
        [class.bps-tooltip-disabled]="bpsTooltipDisabled"
        [ngClass]="_classMap"
        [ngStyle]="bpsOverlayStyle"
        [@.disabled]="!!noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-tooltip-content">
          <div class="ant-tooltip-arrow">
            <span class="ant-tooltip-arrow-content bps-tooltip-arrow-{{bpsTooltipType}}" [ngStyle]="_contentStyleMap"></span>
          </div>
          <div class="ant-tooltip-inner bps-tooltip-inner-{{bpsTooltipType}}" [ngStyle]="_contentStyleMap">
            <ng-container *nzStringTemplateOutlet="bpsTitle; context: bpsTitleContext">{{ bpsTitle }}</ng-container>
          </div>
        </div>
      </div>
    </ng-template>
  `,
  preserveWhitespaces: false
})
export class BpsToolTipComponent extends NzTooltipBaseComponent {
  override bpsTitle: NzTSType | null = null;
  override bpsTooltipType: string = 'variation_8a';
  override bpsPopoverType: string = 'variation_8a';
  override bpsTooltipDisabled: boolean = false;
  bpsTitleContext: Object | null = null;

  bpsColor?: string | NzPresetColor;

  _contentStyleMap: NgStyleInterface = {};

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }

  protected isEmpty(): boolean {
    return isTooltipEmpty(this.bpsTitle);
  }

  protected override updateStyles(): void {
    const isColorPreset = this.bpsColor && isPresetColor(this.bpsColor);

    this._classMap = {
      [this.bpsOverlayClassName]: true,
      [`${this._prefix}-placement-${this.preferredPlacement}`]: true,
      [`${this._prefix}-${this.bpsColor}`]: isColorPreset
    };

    this._contentStyleMap = {
      backgroundColor: !!this.bpsColor && !isColorPreset ? this.bpsColor : null
    };
  }
}
