import { FocusMonitor } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { NzFormStatusService } from 'ng-zorro-antd/core/form';
import { BooleanInput, NzSafeAny, OnChangeType, OnTouchedType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

export type BpsCheckboxType = 'variation1' | 'variation2' | 'variation3';

import { NzCheckboxWrapperComponent } from './bps-checkbox-wrapper.component';

@Component({
  selector: '[bps-checkbox]',
  exportAs: 'bpsCheckbox',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <span
      class="ant-checkbox"
      [class.ant-checkbox-checked]="bpsChecked && !bpsIndeterminate"
      [class.ant-checkbox-disabled]="bpsDisabled"
      [class.bps-checkbox-variation1]="bpsType === 'variation1'"
      [class.bps-checkbox-variation3]="bpsType === 'variation3'"
      [class.ant-checkbox-indeterminate]="bpsIndeterminate"
    >
      <input
        #inputElement
        type="checkbox"
        class="ant-checkbox-input"
        [attr.autofocus]="bpsAutoFocus ? 'autofocus' : null"
        [attr.id]="bpsId"
        [checked]="bpsChecked"
        [ngModel]="bpsChecked"
        [disabled]="bpsDisabled"
        (ngModelChange)="innerCheckedChange($event)"
      />
      <span class="ant-checkbox-inner">
        <ng-container *ngIf="bpsType === 'variation1' || bpsType === 'variation3'">
          <ng-container *ngTemplateOutlet="content"></ng-container>
        </ng-container>
      </span>
    </span>
    <ng-container *ngIf="bpsType === 'variation2'">
      <ng-container *ngTemplateOutlet="content"></ng-container>
    </ng-container>
    <ng-template #content><span><ng-content></ng-content></span></ng-template>
  `,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BpsCheckboxComponent),
      multi: true
    }
  ],
  styleUrls: ['./bps-checkbox.component.css'],
  host: {
    class: 'ant-checkbox-wrapper',
    '[class.ant-checkbox-wrapper-in-form-item]': '!!bpsFormStatusService',
    '[class.ant-checkbox-wrapper-checked]': 'bpsChecked',
    '[class.bps-checkbox-variation3]': `bpsType === 'variation3'`,
    '[class.ant-checkbox-rtl]': `dir === 'rtl'`
  }
})
export class BpsCheckboxComponent implements OnInit, ControlValueAccessor, OnDestroy, AfterViewInit {
  static ngAcceptInputType_bpsAutoFocus: BooleanInput;
  static ngAcceptInputType_bpsDisabled: BooleanInput;
  static ngAcceptInputType_bpsIndeterminate: BooleanInput;
  static ngAcceptInputType_bpsChecked: BooleanInput;

  dir: Direction = 'ltr';
  private destroy$ = new Subject<void>();
  private isbpsDisableFirstChange: boolean = true;

  onChange: OnChangeType = () => { };
  onTouched: OnTouchedType = () => { };
  @ViewChild('inputElement', { static: true }) inputElement!: ElementRef<HTMLInputElement>;
  @Output() readonly bpsCheckedChange = new EventEmitter<boolean>();
  @Input() bpsValue: NzSafeAny | null = null;
  @Input() @InputBoolean() bpsAutoFocus = false;
  @Input() @InputBoolean() bpsDisabled = false;
  @Input() @InputBoolean() bpsIndeterminate = false;
  @Input() @InputBoolean() bpsChecked = false;
  @Input() bpsId: string | null = null;
  @Input() bpsType: BpsCheckboxType = 'variation2';

  innerCheckedChange(checked: boolean): void {
    if (!this.bpsDisabled) {
      this.bpsChecked = checked;
      this.onChange(this.bpsChecked);
      this.bpsCheckedChange.emit(this.bpsChecked);
      if (this.bpsCheckboxWrapperComponent) {
        this.bpsCheckboxWrapperComponent.onChange();
      }
    }
  }

  writeValue(value: boolean): void {
    this.bpsChecked = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }

  setDisabledState(disabled: boolean): void {
    this.bpsDisabled = (this.isbpsDisableFirstChange && this.bpsDisabled) || disabled;
    this.isbpsDisableFirstChange = false;
    this.cdr.markForCheck();
  }

  focus(): void {
    this.focusMonitor.focusVia(this.inputElement, 'keyboard');
  }

  blur(): void {
    this.inputElement.nativeElement.blur();
  }

  constructor(
    private ngZone: NgZone,
    private elementRef: ElementRef<HTMLElement>,
    @Optional() private bpsCheckboxWrapperComponent: NzCheckboxWrapperComponent,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor,
    @Optional() private directionality: Directionality,
    @Optional() public bpsFormStatusService?: NzFormStatusService
  ) { }

  ngOnInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .pipe(takeUntil(this.destroy$))
      .subscribe(focusOrigin => {
        if (!focusOrigin) {
          Promise.resolve().then(() => this.onTouched());
        }
      });
    if (this.bpsCheckboxWrapperComponent) {
      this.bpsCheckboxWrapperComponent.addCheckbox(this);
    }

    this.directionality.change.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;

    this.ngZone.runOutsideAngular(() => {
      fromEvent(this.elementRef.nativeElement, 'click')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => {
          event.preventDefault();
          this.focus();
          if (this.bpsDisabled) {
            return;
          }
          this.ngZone.run(() => {
            this.innerCheckedChange(!this.bpsChecked);
            this.cdr.markForCheck();
          });
        });

      fromEvent(this.inputElement.nativeElement, 'click')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => event.stopPropagation());
    });
  }

  ngAfterViewInit(): void {
    if (this.bpsAutoFocus) {
      this.focus();
    }
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    if (this.bpsCheckboxWrapperComponent) {
      this.bpsCheckboxWrapperComponent.removeCheckbox(this);
    }

    this.destroy$.next();
    this.destroy$.complete();
  }
}
